// This file contains all styles related to gallery component.
// -----------------------------------------------------------------------------

/**
* Gallery Component
*/
.home_gallery {
    @include media-query(medium) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 40px;
        row-gap: 20px;
        margin-top: 40px;
    }

    @include media-query(large) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 50px;
        row-gap: 30px;
        background: $secondary-color;
        padding: 50px;
        border-radius: 25px;
        margin-bottom: 40px;
    }
    
    @include media-query(xlarge) {
        row-gap: 40px;
        column-gap: 60px;
    }
}