// -----------------------------------------------------------------------------
// This file contains all styles related to tag component.
// -----------------------------------------------------------------------------

/**
* Tag Component
*/
.tag-container {
    display: flex;
    &__button {
      @include flex-center;
      background: $primary-color;
      color: $tertiary-color;
      margin-top: 4%;
      @include font(10px, 500);
     
      height: 18px;
      border-radius: 5px;
      margin-right: 10px;
      padding: 2px 10px 0 10px;
  
      @media screen and (min-width: $breakpoint-desktop) {
        flex-direction: column;
         width: 100%;
        height: 25px;
        border-radius: 10px;
        @include font(14px, 500);
      }
    }
  }